import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { CepLocalidade, CLASS_NAME } from '../../../../models/dtos/ger/cepLocalidade';
import { TIPO_CIDADE_ENUM_VALUES } from '../../../../models/enumerated/mlf/tipoCidadeEnum';
import CepUFIS from '../../../components/ger/cepUFIS/CepUFIS';

interface CepLocalidadeEditViewProps extends EditViewProps<CepLocalidade> {}

const CepLocalidadeEditView: FC<CepLocalidadeEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<CepLocalidade>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Manutenção de Cidade">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <CepUFIS
              formProps={{ ...formProps, validation: { required: true } }}
              label="UF"
              placeholder="Selecione uma UF"
              id="uf"
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: false } }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="codigoMunicipio"
              inputProps={true}
              label="Cód. Município"
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 7), required: true } }}
              isPrependLabel={true}
              placeholder="Informe o Código do Município"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="nome"
              isPrependLabel={true}
              formProps={{ ...formProps, validation: { required: true, maxLength: 60 } }}
              label="Nome"
              placeholder="Informe o Nome"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="tipo"
              label="Tipo"
              formProps={{ ...formProps }}
              isPrependLabel={true}
              placeholder="Informe o Tipo de Localidade"
              inputDropDownProps={{ optionsProvider: TIPO_CIDADE_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="cep"
              isPrependLabel={true}
              formProps={{ ...formProps }}
              label="CEP"
              type={InputType.CEP}
              placeholder="Informe o CEP"
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default CepLocalidadeEditView;
